body {
  --border-color: #e8e8eb;
  --min-width: 32em;
  word-break: keep-all;
  margin: 0;
  padding: 0;
  font-size: 24px;
  min-width: var(--min-width);
  line-height: 1.5em;
  box-sizing: border-box;
}
.logo {
  width: 2em;
  height: 2em;
}

.app-text {
  width: 5em;
  height: 2em;
}

.community {
  width: 1.5em;
  height: 1.5em;
}

header > nav {
  border-bottom: 1px solid var(--border-color);
  box-shadow: 2px 2px 6px 0 rgba(0, 0, 0, 0.1);
  padding: 8px 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu {
  display: flex;
  align-items: center;
}
.menu .menu-item {
  margin: 0 0.25em;
}
.conditions {
  display: flex;
  align-items: center;
  min-width: var(--min-width);
}

@media screen and (max-width: 768px) {
  .conditions {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
  body {
    font-size: 12px;
  }
}
@media screen and (min-width: 36em) {
  main {
    padding: 2em;
  }
}
.condition {
  margin: 0.25em 1em;
  display: flex;
  align-items: baseline;
}
.actions {
  min-width: fit-content;
}
.condition label {
  margin-right: 0.5em;
  width: max-content;
}
.condition label::after {
  content: ":";
}
.products {
  display: inline-flex;
  width: 50%;
  flex-wrap: wrap;
  flex-shrink: 1;
  justify-content: center;
  align-items: center;
}
.product {
  display: inline-block;
  box-sizing: border-box;
  border: 2px solid var(--border-color);
  height: 2em;
  margin: 0.25em;
  border-radius: 4px;
  padding: 0.25em;
}
.choices {
  display: flex;
  min-width: var(--min-width);
  justify-content: space-around;
  width: 100%;
  height: 8em;
}
.choice {
  display: inline-flex;
  justify-content: center;
}
.choice svg {
  cursor: pointer;
  width: 8em;
  height: 8em;
  transition: all 0.3s ease-in-out;
}
.choice svg:hover, .choice:focus svg {
  background-color: #ced4da;
}
.node {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.null p.info,
.conclusion p.info {
  height: 12em;
}
.compare {
  display: flex;
  grid-template-columns: 1fr 2em 1fr;
  align-items: center;
  justify-content: space-around;
  width: 100%;
}
code {
  --color-fg-default: #24292f;
  --color-neutral-muted: rgba(175, 184, 193, 0.2);
  color: var(--color-fg-default);
  word-wrap: break-word;
  box-sizing: border-box;
  line-height: 1em;
  padding: 0.2em 0.4em;
  margin: 0;
  background-color: var(--color-neutral-muted);
  border-radius: 0.25em;
}
.weigh-history {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
