input {
  display: block;
  width: 100%;
  padding: 0.375em 0.75em;
  font-size: 1em;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  appearance: none;
  border-radius: 0.25em;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
input:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25em rgb(13 110 253 / 25%);
}
select {
  cursor: pointer;
  box-sizing: border-box;
  margin: 0;
  font-family: inherit;
  text-transform: none;
  word-wrap: normal;
  display: block;
  width: 100%;
  padding: 0.375em 2.25em 0.375em 0.75em;
  font-size: 1em;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M2 5l6 6 6-6'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 0.75em center;
  background-size: 16px 12px;
  border: 1px solid #ced4da;
  border-radius: 0.25em;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  appearance: none;
  margin-bottom: 0;
}
select:focus {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25em rgb(13 110 253 / 25%);
}
option {
  font-family: inherit;
  text-transform: none;
  word-wrap: normal;
  font-size: 1em;
  line-height: 1.5;
  color: #212529;
  box-sizing: border-box;
}
.alert-danger {
  color: #842029;
  background-color: #f8d7da;
  border-color: #f5c2c7;
}

.alert {
  position: relative;
  padding: 1em 1em;
  margin-bottom: 1em;
  border: 1px solid transparent;
  border-radius: 0.25em;
}
